import React, { Component } from 'react';
import {Card} from "antd";
const { Meta } = Card;

class HomeInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      categories:[],
      products:[]
    }
  }
  componentDidMount(){
    fetch('https://intellgentcms.herokuapp.com/api/getMainCategories')
    .then((response) => response.json())
    .then((responseJson) => {
      fetch('https://intellgentcms.herokuapp.com/api/getMainProducts')
      .then((response2) => response2.json())
      .then((responseJson2) => {
        this.setState({categories:responseJson.categories,products:responseJson2.products});
      })
      .catch((error2) => {
        console.error(error2);
      });
    })
    .catch((error) => {
      console.error(error);
    });
  }
  showCategories=()=>{
    return this.state.categories.map((cat,i)=>{
      return(
        <div className = "home-thumbnail" key = {i} onClick={()=>{this.props.productsSelected(cat.type,cat.name)}}>
          <div className = "home-thumbnail-pic" style = {{  backgroundImage: "url('"+cat.image+"')"}}>
          </div>
          <div className = "home-thumbnail-des">
            <h4><b>{cat.name}</b></h4>
            <p>{cat.description}</p>
          </div>
        </div>
      )
    })
  }
  showProducts=()=>{
    return this.state.products.map((prod,i)=>{
      return(
        <div className ="product"
          key = {i}
          onClick={()=>{this.props.productSelected(prod._id,prod.category,"category")}}
          >
            <Card
              style={{ width: 300 }}
              cover={<img alt="Imagen del producto" src={prod.image} style={{ width: "100%", height: "40vh",marginLeft:"auto",marginRight:"auto",display:"block"}} />}
              key ={i}
              >
                <Meta
                  className= "meta"
                  title={prod.name}
                />
                <Meta
                  className= "meta"
                  title={prod.reference}
                />
                {
                (prod.oldPrice &&prod.oldPrice!=="0")&&(
                  <Meta
                    className= "meta old-price "
                    description={"$ "+Number(prod.oldPrice).toLocaleString('en', {useGrouping:true})}
                  />
                )
                }

                <Meta
                  className= "meta price"
                  description={"$ "+Number(prod.price).toLocaleString('en', {useGrouping:true})}
                />
            </Card>
          </div>
        )
      })
    }
    handleClick = (e)=>{
      this.props.productsSelected(e.split("::")[0],e.split("::")[1]);
    }
    render() {
      return (
        <div>
          <div className = "products-title-home" style = {{margin:"0 auto"}}>Productos destacados</div>
          <div

            className= "homeinfo-products-wrapper"
            >
              {
                this.showProducts()
              }
            </div>
            <div className = "products-title-home" style = {{margin:"0 auto"}}>Productos por categorías</div>
            <div
              className= "homeinfo-categories-wrapper"
              >

                {
                  this.showCategories()
                }
              </div>

            </div>

          );
        }
      }

      export default HomeInfo;
