import React, { Component } from 'react';
import './App.css';
import { Card, Icon} from 'antd';
const { Meta } = Card;
class Brands extends Component {
  constructor(props) {
    super(props);
    this.state = {
      brands : []
    };

  }
  componentDidMount(){
    fetch("https://intellgentcms.herokuapp.com/api/getBrands", {
      method: 'GET',
      headers: {
        'x-access-token':localStorage.getItem("IntToken")
      }
    }).then( response => response.json()).then((json)=>{
      this.setState({brands:json.brands})
    }
  ).catch(err => console.error(err));
  }
  render() {
    return (
      <div>
        <div className = "products-title">Nuestras marcas</div>
        <div
          className= "products-wrapper"
          >
            {this.showBrands()}
          </div>
        </div>
      );
    }
    handleClick = (link)=>{
      window.location.href = link;
    }
    showBrands=()=>{
      return this.state.brands.map((brand,i)=>{
        return(

          <div
            key ={i}

            className = "product">

            <Card
              style={{ width: 300 }}
              cover={<img alt="Imagen de la marca" src={brand.image} style={{ width: "100%", height: "40vh",marginLeft:"auto",marginRight:"auto",display:"block"}} />}
              key ={i}
              onClick = {()=>{this.props.brandSelected(brand.name)}}
              >
                <Meta
                  className= "meta"
                  title={brand.name}
                />
              </Card>

            </div>
          )
        })
      }

    }
    export default Brands;
