import React, { Component } from 'react';
import Carousel from 'nuka-carousel';

class CarrouselWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      brands:[],
      title: "",
      items:[],
    };
  }
  componentDidMount(){
    //fetch('/api/getCarouselItems)
    fetch('https://intellgentcms.herokuapp.com/api/getCarouselItems')
    .then((response) => response.json())
    .then((responseJson) => {
      if(responseJson){
        this.setState({items:responseJson.items});
      }
    })
    .catch((error) => {
      console.error(error);
    });
  }
  showItems = () =>{
    if(this.state.items.length>0){
    return this.state.items.map((item, i)=>{
      return (
        <div key = {i} className=" item-c" onClick ={()=>{this.props.productSelected(item.product_id,item.category,"category")}} style = {{background:"url('"+item.image+"')"  }}>
          <div className="sobre-c">
            {/* <p className= "descr-sobre-c">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore</p> */}
          </div>
        </div>
      )
    })
    }
    else return(
      <div className="item-c-1 item-c">
        <div className="sobre-c">
          {/* <p className= "descr-sobre-c"> Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit</p> */}
        </div>
      </div>
    )
  }
  render() {
    return (
      <div
        className= "carrusel-wrapper"
        >
        <Carousel
          className= "carrusel"
          autoplay={true}
          autoplayInterval={3000}
          wrapAround ={true }
          renderCenterLeftControls={({ previousSlide }) => (
            <button onClick={previousSlide} style = {{display:"none"}}></button>
          )}
          renderCenterRightControls={({ nextSlide }) => (
            <button onClick={nextSlide}  style = {{display:"none"}}></button>
          )}
          >
            {this.showItems()}
          {/*
          <div className="item-c-1  item-c" onClick ={()=>{}}>
            <div className="sobre-c">
              <p className= "descr-sobre-c">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore</p>
            </div>
          </div>
          <div className="item-c-2  item-c">
            <div className="sobre-c">
              <p className= "descr-sobre-c"> Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam</p>
            </div>
          </div>
          <div className="item-c-3 item-c">
            <div className="sobre-c">
              <p className= "descr-sobre-c"> Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit</p>
            </div>
          </div> */}
        </Carousel>
      </div>
    );
  }
}

export default CarrouselWrapper;
