import React, { Component } from 'react';
import './App.css';
import { Card, Icon} from 'antd';
import * as qs from 'query-string';
const { Meta } = Card;
const params = qs.parse(window.location.search);
class Products extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products:[]
    };

  }
  componentDidMount(){
    if(params.type){

    fetch("https://intellgentcms.herokuapp.com/api/get"+params.type+"?category="+params.category, {
      method: 'GET',
      headers: {
        'x-access-token':localStorage.getItem("IntToken")
      }
    }).then( response => response.json()).then((json)=>{
      this.setState({products:json.products});
    }
  ).catch(err => console.error(err));
  }
  else if(params.brand) {
    fetch("https://intellgentcms.herokuapp.com/api/getProductsByBrand?brand="+params.brand, {
      method: 'GET',
      headers: {
        'x-access-token':localStorage.getItem("IntToken")
      }
    }).then( response => response.json()).then((json)=>{
      this.setState({products:json.products});
    }
  ).catch(err => console.error(err));
  }
  else if(params.query) {
    fetch("https://intellgentcms.herokuapp.com/api/search?search="+params.query, {
      method: 'GET',
      headers: {
        'x-access-token':localStorage.getItem("IntToken")
      }
    }).then( response => response.json()).then((json)=>{
      this.setState({products:json.products});
    }
  ).catch(err => console.error(err));
  }
}
render() {
  return (
    <div>
      <div className = "products-title">{(params.category||params.brand||params.query)}</div>
      <div
        className= "products-wrapper"
        >
          {this.showProducts()}
        </div>
      </div>
    );
  }
  handleClick = (link)=>{
    window.location.href = link;
  }
  showProducts=()=>{
    if(this.state.products.length > 0){

    return this.state.products.map((prod,i)=>{
      return(
        <div
          className = "product"
          key ={i}
          onClick = {()=>{
            if(params.type){
            this.props.productSelected(prod._id, params.category,"category")
            }
            else if (params.brand){
            this.props.productSelected(prod._id, params.brand,"brand")
          }
          else if( params.query){
            this.props.productSelected(prod._id,params.query,"query")

          }
          }}
          >
            <Card
              style={{ width: 300 }}
              cover={<img alt="Imagen del producto" src={prod.image} style={{ width: "100%", height: "40vh",marginLeft:"auto",marginRight:"auto",display:"block"}} />}
              key ={i}
              >
                <Meta
                  className= "meta"
                  title={prod.name}
                />
                <Meta
                  className= "meta"
                  title={prod.reference}
                />
                <Meta
                  className= "meta"
                  description={prod.bDescription}
                />
              </Card>
            </div>
          )
        })
      }

      else return(
          <div style = {{margin:"0 auto", padding:"100px 0","textAlign":"center"}}>Lo sentimos, no hay prouctos que coincidan con esa búsqueda</div>

      )
    }

    }
    export default Products;
