import React, { Component } from 'react';
import './App.css';
import './animate.css';
import Header from "./Header"
import HomeInfo from "./HomeInfo.js"
import CarouselWrapper from "./CarouselWrapper"
import Footer from "./Footer"
import Products from "./Products"
import Brands from "./Brands"
import Error from "./Error"
import ProductDetail from "./ProductDetail"
import Contact from "./Contact"

import {BrowserRouter, Route, Switch} from "react-router-dom"
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {

    };
  }
  productsSelected=(type, category)=>{
    window.location.href = "/products?type="+type+"&category="+category;
  }
  productSelected=(_id,c,param)=>{
    if(param === "category")
    window.location.href = "/product?category="+c+"&_id="+_id;
    else if(param === "brand")
    window.location.href = "/product?brand="+c+"&_id="+_id;
    else
    window.location.href = "/product?query="+c+"&_id="+_id;

  }
  brandsSelected=(brands, title)=>{
    window.location.href = "/brands";
  }
  brandSelected=(brand)=>{
    window.location.href ="/products?brand="+brand;
  }
  productsSearched=(query)=>{
    if(query.trim()!== "")
    window.location.href ="/products?query="+query;
  }
  render() {
    return (
      <div className =" animated fadeIn">
        <Header productsSelected ={this.productsSelected} brandsSelected={this.brandsSelected} productsSearched= {this.productsSearched}/>
        <BrowserRouter>
          <Switch>
            <Route
              path = "/"
              render={(props) => (<div><CarouselWrapper productSelected ={this.productSelected}/><HomeInfo  productsSelected ={this.productsSelected} productSelected ={this.productSelected} /></div>)}
              exact
            />
            <Route
              path = "/products/"
              render={(props) => <Products products = {this.state.products} title = {this.state.title} productSelected = {this.productSelected} />}
            />
            <Route
              path = "/product"
              render={(props) => <ProductDetail productsSelected = {this.productsSelected} brandSelected = {this.brandSelected} productsSearched= {this.productsSearched}/>}
              exact
            />
            <Route
              path = "/brands"
              render={(props) => <Brands brands = {this.state.brands} title = {this.state.title} brandSelected = {this.brandSelected}/>}
              exact
            />
            <Route
              component={Contact}
            />
            <Route
              component={Error}
            />
          </Switch>
        </BrowserRouter>

        <Footer productsSelected ={this.productsSelected}/>

      </div>
    );
  }
}

export default App;
