import React, { Component } from 'react';
import './App.css';
import { Card, Icon} from 'antd';
import * as qs from 'query-string';
const { Meta } = Card;
const params = qs.parse(window.location.search);

class ProductDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data : {characteristics:"", price:0, sImages:""}
    };

  }
  componentWillMount(){
    //fetch('/api/getCorporateProductsCategories')
    fetch('https://intellgentcms.herokuapp.com/api/getProduct?_id='+params._id)
    .then((response) => response.json())
    .then((responseJson) => {
      if(responseJson){
        this.setState({data:responseJson.product});
      }
      else{
      }
    })
    .catch((error) => {
      console.error(error);
    });
  }
  showCharacteristics = ()=>{
    return this.state.data.characteristics.split("::").map((ch,i)=>{
      return (<li key = {i} className ="product-detail-ch">  {ch}</li>)
    })
  }
  showSecondImages = ()=>{
    return(
      <div className = "product-detail-second-images-wrapper">
        <div style = {{background: "url('"+this.state.data.image+"')"}} className = "product-detail-second-image" onMouseOver={()=>{this.handleMouseHoverImage(this.state.data.image)}}></div>
        {
          this.state.data.sImages.split("))((").map((image,i)=>{
            return (<div key = {i} style = {{background: "url('"+image+"')"}} className = "product-detail-second-image" onMouseOver={()=>{this.handleMouseHoverImage(image)}}></div> )
          })
        }
      </div>
    )
  }
  handleMouseHoverImage = (image)=>{
    document.querySelector(".product-detail-image").style.background ="url('"+image+"')";
  }
  handleBuy = ()=>{
    window.open(this.state.data.link, '_blank');
  }
  render() {
    return (
      <div className = "product-detail">
        <div>
          <div style = {{paddingBottom:"10px"}}>
            <a href = "/">Inicio/</a>
            {params.category?(
              <a onClick = {()=>{this.props.productsSelected(this.state.data.type,this.state.data.category)}}>{params.category}/</a>
            ):
            <span>
              {params.brand?(
                <a onClick = {()=>{this.props.brandSelected(params.brand)}}>{params.brand}/</a>
              ):
              <a onClick = {()=>{this.props.productsSearched(params.query); console.log(params);}}>Búsqueda/</a>

            }
          </span>
        }

        <span>{this.state.data.name}</span>
      </div>
      <div style = {{background: "url('"+this.state.data.image+"')"}} className = "product-detail-image"></div>
      {this.showSecondImages()}
    </div>
    <div className = "product-detail-info">
      <div>
        <span className = "product-detail-name">{this.state.data.name}</span>

        {this.state.data.specs!=="none"&&(<a href={this.state.data.specs} download={this.state.data.name + ".pdf"} className = "product-detail-specs">(Especificaciones)</a>)}
      </div>
      <div className = "product-detail-name">{this.state.data.reference}</div>


      {
        (this.state.data.oldPrice&&this.state.data.oldPrice!=="0")&&(

          <div className = "product-detail-price" style = {{textDecoration:"line-through", borderBottom:"none"}} >{"$ "+Number(this.state.data.oldPrice).toLocaleString('en', {useGrouping:true})}</div>
        )
      }
      <div className = "product-detail-price">{"$ "+Number(this.state.data.price).toLocaleString('en', {useGrouping:true})}</div>
      <div className = "product-detail-description">{this.state.data.dDescription}</div>
      <ul>
        {this.showCharacteristics()}
      </ul>
      {
          (this.state.data.link)&&
          (
            <button  className="btn"   onClick = {this.handleBuy} style ={{"width":"30%"}}>Comprar</button>

          )
      }
    </div>
  </div>
);
}

}
export default ProductDetail;
