import React, { Component } from 'react';
import { Menu, Icon } from 'antd';
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class Header extends Component {

  constructor(props) {
    super(props);
    this.state = {
      current: 'mail',
      corporateCategories:[],
      homeCategories:[],
    }
  }
  componentDidMount(){
    window.addEventListener('scroll', this.handleScroll);
    //fetch('/api/getCorporateProductsCategories')
    fetch('https://intellgentcms.herokuapp.com/api/getCorporateProductsCategories')
    .then((response) => response.json())
    .then((responseJson) => {
      if(responseJson){
        this.setState({corporateCategories:responseJson.categories});
        //fetch('/api/getHomeProductsCategories')
        fetch('https://intellgentcms.herokuapp.com/api/getHomeProductsCategories')
        .then((response) => response.json())
        .then((responseJson) => {
          if(responseJson){
            this.setState({homeCategories:responseJson.categories});
          }
          else{
          }
        })
        .catch((error) => {
          console.error(error);
        });
      }
      else{
      }
    })
    .catch((error) => {
      console.error(error);
    });
  }
  handleScroll = (event)=>{
    if( document.documentElement.scrollTop>=110){
      document.querySelector(".navbar-fixed-top").classList.add("downnav");
      document.querySelector("#search").classList.add("downnav-search");

    }
    else{
      document.querySelector(".navbar-fixed-top").classList.remove( "downnav");
      document.querySelector("#search").classList.remove("downnav-search");

    }
  }
  handleClick = (e) => {
    this.setState({
      current: e.key,
    });
    if(e.key==="inicio"){
      window.location.href = "/";
    }
  }
  showHomeCategories = ()=>{
    return this.state.homeCategories.map((cat,i)=>{
      return (
        <Menu.Item key={cat.name} onClick = {this.homeCategorySelected}>{cat.name}</Menu.Item>
      )
    })
  }
  showCorporateCategories = ()=>{
    return this.state.corporateCategories.map((cat,i)=>{
      return (
        <Menu.Item key={cat.name} onClick = {this.corporateCategorySelected}>{cat.name}</Menu.Item>
      )
    })
  }
  homeCategorySelected = (e)=>{
    this.props.productsSelected("HomeProducts", e.key);
  }

  corporateCategorySelected = (e)=>{
    this.props.productsSelected("CorporateProducts", e.key);
  }
  brandsSelected = (e)=>{
    this.props.brandsSelected();
  }

  render() {
    return (
      <div>
        <div className = "pre-wrapper">
          <div className = "pre-nav container">
            <span > Dirección: Cra 15 No. 77 - 05, Local 204 Bogotá D.C. </span>
            <span > Horario de atención: Lun - Sáb 9:00am - 8:00pm, Dom: 10:00am - 5:00pm</span>
            <span > PBX: (571) 743 66 43</span>
          </div>
        </div>
        <div  id ="search">
          <form onSubmit= {(event)=>{event.preventDefault();this.props.productsSearched(this.refs.query.value);}}>
          <input id ="search-input" className="form-control text-input" ref = "query" placeholder="Buscar" style ={{width:"10vw","display":"inline", borderRadius:".25rem", height:"30px", marginRight:"10px"}} />
          <button  type="submit" id ="search-buttom" className="btn"   style ={{"width":"auto",textAlign:"center", height:"30px"}}><Icon type="search" style = {{margin:0}} /></button>
        </form>
      </div>
      <nav className="navbar navbar-default navbar-fixed-top">
        <div className="container">
          <div className="navbar-header">
            <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
              <span className="sr-only">Toggle navigation</span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
            <span className = "navbar-img navbar-brand" onClick = {()=>{window.location.href = "/"}}></span>
          </div>
          <div id="navbar" className="collapse navbar-collapse">
            <ul className="nav navbar-nav" id = "bar-buttons">
              <Menu
                onClick={this.handleClick}
                selectedKeys={[this.state.current]}
                mode="horizontal"
                id="navbar-menu"
                >
                  <Menu.Item key="inicio">
                    <Icon type="home"/>Inicio
                  </Menu.Item>
                  <SubMenu title={<span><Icon type="folder" />Productos</span>}>
                  {/* <Menu.Item key="todos:hogar">Todos los productos</Menu.Item> */}
                  <MenuItemGroup title="Hogar">
                    {this.showHomeCategories()}
                  </MenuItemGroup>
                  <MenuItemGroup title="Corporativo">
                    {this.showCorporateCategories()}
                  </MenuItemGroup>
                </SubMenu>
                <Menu.Item key="marcas" onClick = {this.brandsSelected}>
                  <Icon type="menu-fold" />Nuestras marcas
                </Menu.Item>
                <Menu.Item key="nosotros" onClick = {()=>{
                  window.location.href ="/contact";
                }
              }>
              <Icon type="question-circle-o" />Contáctenos
            </Menu.Item>

          </Menu>

        </ul>

      </div>
    </div>
  </nav>

</div>

);
}
}

export default Header;
