import React, { Component } from 'react';
import Icon from "antd";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errorMessage :"",
      loading:false
    };
  }
  handleSubmit = (e)=>{

    let userName = this.refs.userName.value.trim(),
    userEmail = this.refs.userEmail.value.trim(),
    subject = this.refs.subject.value.trim(),
    message = this.refs.message.value.trim()

    e.preventDefault();
    if(userName!== ""
    && userEmail!== ""
    && subject!== ""
    && message !== ""
  ){
    this.setState({errorMessage:"",loading:true});
    //fetch('/api/sendEmail', {

    fetch('https://intellgentcms.herokuapp.com/api/sendEmail', {
      method: 'POST',
      body: JSON.stringify({userName,userEmail,subject,message}),
      headers: { 'Content-Type': 'application/json' },
      contentType: 'application/json'
    }).then((res)=> {
      this.setState({loading:false})
      window.alert("Mensaje enviado");
      window.location.href = "/";

    }).catch((error)=> {
      console.error(error);

    });

  }
  else {
    this.setState({errorMessage:"Llene todos los campos"})
  }

}
render() {
  return (
    <div >
      <div className = "products-title">Contáctenos</div>
      <div
        className= "products-wrapper contact"
        >
          <form className = "contact-form"
            onSubmit = {this.handleSubmit}
            >
              <div className = "contact-form-item">
                <label className ="contact-label">Nombre</label>
                <input  className="form-control text-input" style ={{marginLeft :"10px",width:"300px",display:"inline"}} ref = "userName" placeholder="Nombre"  />
              </div>
              <div className = "contact-form-item">
                <label className ="contact-label">Email</label>
                <input  className="form-control text-input" style ={{marginLeft :"26px",width:"300px",display:"inline"}} ref = "userEmail" placeholder="Email"  />
              </div>
              <div className = "contact-form-item">
                <label className ="contact-label">Asunto</label>
                <input  className="form-control text-input" style ={{marginLeft :"17px",width:"300px",display:"inline"}} ref = "subject" placeholder="Aunto"  />
              </div>
              <div className = "contact-form-item">
                <label className ="contact-label" style = {{verticalAlign:"top"}}>Mensaje</label>
                <textarea  type="text" ref = "message" placeholder = "Mensaje" className="form-control text-input" style ={{marginLeft :"6px",width:"300px",display:"inline"}}  rows = "5"></textarea>
              </div>
              <label style ={{color:"red", display:"block"}} >{this.state.errorMessage}</label>
              {(!this.state.loading)&&(
                <button  className="btn" type="submit"  style ={{"width":"30%"}}>Enviar</button>
              )
            }

          </form>
        </div>
      </div>
    );
  }
}

export default App;
